.movie-container {
    max-width: 342px;
    background-color: black;
    border-radius: 25px;
    margin: 30px 30px;
}

.movie-header {
    background-color: var(--secondary-color);
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--main-color);
    padding: 0 10px;
}

.btn-movie {
    width: 100%;
    height: 40px;
    border: none;
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
    background-color: var(--light-main);
    font-size: 20px;
    margin: 0;
    color: var(--main-color);
    font-family: 'Lato', sans-serif;
    letter-spacing: 2px;
    font-weight: 600;
    text-transform: uppercase;
    text-shadow: 1.5px 1.5px 3px rgb(8, 8, 8);
    cursor: pointer;
}

.poster {
    margin: 0;
}


.movie-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 80vw;
    margin: 0 auto;
}
header {
    background-color: var(--light-secondary);
    height: 80px;
    display: flex;
    align-items: center;
    padding: 0 50px;
    color: var(--main-color);
    justify-content: center;
    font-family: 'Lato', sans-serif;
    font-size: 24px;
    letter-spacing: 3px;
    text-shadow: 2px 2px 4px rgb(44, 44, 44);
}

header h1 {
    margin: 0;
}
.hall {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.screen {
    width: 900px;
    height: 40px;
    background-color: bisque;
    margin: 30px auto;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
}

.seat {
    width: 40px;
    height: 40px;
    background-color: rgb(156, 7, 7);
    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
    cursor: pointer;
}

.seat:hover {
    -webkit-transform: scale(0.85);
    transform: scale(0.85);
}

.selectedSeat {
    background-color: rgb(233, 12, 12);
}

.reservedSeat {
    background-color: var(--light-secondary);
}

.row {
    display: flex;
    justify-content: center;
}

.rowName {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    margin: 5px;
    color: white;
}

#btn-reservation {
    background-color: white;
    color: rgb(8, 8, 8);
    font-family: 'Lato', sans-serif;
    letter-spacing: 2px;
    font-size: 22px;
    font-weight: 600;
    width: 150px;
    height: 50px;
    border: none;
    border-radius: 25px;
    margin: 20px auto;
    cursor: pointer;
}
#popup {
    display: none;
    height: 60vh;
    width: 60vw;
    position: fixed;
    top: 20vh;
    left: 20vw;
    background-color: var(--light-main);
    color: var(--main-color);
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 25px;
    font-family: 'Lato', sans-serif;
    letter-spacing: 2px;
    text-shadow: 1.5px 1.5px 3px rgb(8, 8, 8);
}

#popup button {
    width: 100px;
    height: 50px;
    border: none;
    background-color: var(--main-color);
    color: var(--secondary-color);
    font-family: 'Lato', sans-serif;
    letter-spacing: 1.5px;
    font-size: 14px;
    font-weight: 600;
    border-radius: 25px;
    cursor: pointer;
}
.form-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

form {
    width: 70vw;
    height: 70vh;
    background-color: rgb(97, 2, 2);
    margin-top: 50px;
    border-radius: 25px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    color: white;
    font-family: 'Lato', sans-serif;
    letter-spacing: 2px;
    text-shadow: 1.5px 1.5px 3px rgb(8, 8, 8);
}

.form-panel {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 60%;
}

.date-container {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 70%;
}

.date {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 400px;
}

.hour {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 400px;
}

.tickets-container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 50%;
}

#btn-tickets {
    background-color: white;
    color: rgb(8, 8, 8);
    font-family: 'Lato', sans-serif;
    letter-spacing: 2px;
    font-size: 22px;
    font-weight: 600;
    width: 150px;
    height: 50px;
    border: none;
    border-radius: 25px;
    margin: 20px auto;
    cursor: pointer;
}
:root {
    --main-color: rgb(255, 255, 255);
    --light-main: rgb(97, 2, 2);
    --secondary-color: rgb(8, 8, 8);
    --light-secondary: rgb(100, 97, 97);
}

* {
    margin: 0;
    padding: 0;
}

body {
    background-color: rgb(44, 44, 44);
    text-align: center;
    font-family: 'Lato', sans-serif;
    letter-spacing: 2px;
}

a {
    text-decoration: none;
    color: rgb(255, 255, 255);
    color: var(--main-color);
}

a:visited {
    color: rgb(255, 255, 255);
    color: var(--main-color);
}
